/* ./src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Seleccion */

::selection {
  background: #f2a240; /* WebKit/Blink Browsers */
  color: #363636;
}
::-moz-selection {
  background: #f2a240; /* WebKit/Blink Browsers */
  color: #363636;
}

/* Scrollbar */

::-webkit-scrollbar {
  width: 15px;
}

::-webkit-scrollbar-track {
  background: #1c1f24;
}

::-webkit-scrollbar-thumb {
  background: #61dafb;
  border: 3px solid #1c1f24;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #61dafb;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* active */
.active span {
  border-bottom: 2px solid #61dafb;
}

.active a {
  color: #61dafb;
}

/* rrss */

.rrss {
  box-shadow: inset 0 -0.3666666667em 0 0 #3e6671,
    inset 0 -0.3333333333em 0 0 #3e6671;
  transition: 200ms;
}

.rrss:hover {
  box-shadow: inset 0 -0.666666667em 0 0 #3e6671,
    inset 0 -0.6333333333em 0 0 #3e6671;
}

@media (min-width: 1024px) {
  .top-navbar {
    display: -webkit-inline-box !important;
    display: -ms-inline-flexbox !important;
    display: inline-flex !important;
  }
}

/* Menu icono */

.hamburger .line {
  width: 25px;
  height: 2px;
  background-color: #ffffff;
  display: block;
  margin: 4px auto;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.hamburger:hover {
  cursor: pointer;
}

#hamburger-1.is-active .line:nth-child(2) {
  opacity: 0;
}

#hamburger-1.is-active .line:nth-child(1) {
  -webkit-transform: translateY(6px) rotate(45deg);
  -ms-transform: translateY(6px) rotate(45deg);
  -o-transform: translateY(6px) rotate(45deg);
  transform: translateY(6px) rotate(45deg);
}

#hamburger-1.is-active .line:nth-child(3) {
  -webkit-transform: translateY(-6px) rotate(-45deg);
  -ms-transform: translateY(-6px) rotate(-45deg);
  -o-transform: translateY(-6px) rotate(-45deg);
  transform: translateY(-6px) rotate(-45deg);
}

/* Avatar */

#bloque-img {
  display: block;
  margin: auto;
  position: relative;
  width: 30vh;
  height: 30vh;
}

#avatar {
  display: block;
  margin: auto;
  height: 30vh;
  width: 30vh;
  border-radius: 50%;
  object-fit: cover;
}

#bloque-img:before {
  content: "";
  position: absolute;
  top: -4px;
  left: -4px;
  right: -4px;
  bottom: -4px;
  background: linear-gradient(
    90deg,
    #f5f5f5,
    #25a79a,
    #f5f5f5,
    #f2a240,
    #f5f5f5
  );
  background-size: 400%;
  border-radius: 50%;
  z-index: -1;
  animation: animate 20s linear infinite;
}

@keyframes animate {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 200% 0;
  }
  100% {
    background-position: 0 0;
  }
}
/* Boton */

.aButton {
  display: block;
  text-decoration: none;
  color: #61dafb;
  border: 2px solid #61dafb;
  text-align: center;
  position: relative;
  transition: all 200ms;
}

.aButton span {
  position: relative;
  z-index: 2;
}

.aButton:after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #61dafb;
  transition: all 200ms;
}

.aButton:hover {
  color: #20232a;
}

.aButton:hover:after {
  width: 100%;
}
